import server from '../../server.js';
export default {
	data() {
		return {
			postInfo:[],
			pllist:[],
			dslist:[],
			showVip:false,
			InputBottom: 0,
			plnr:"",
			xzId:1,
			postid:"",
			setvar:"",
			Give:"0",
			huifu:false,
			gaodu:'-710px',
			yanse:'rgba(0,0,0,0)',
			pluid:"",
			monnumber:1,
			shunxu:false
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {// 来自页面内分享按钮
		  console.log(res.target)
		}
		return {
		  title: this.postInfo.Content,
		  path:'/pages/postinfo/postinfo?id='+this.postid,
		  desc:this.postInfo.Content,
		  imageUrl:this.postInfo.PictureId[0]
		}
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		console.log('ID:',option.id); //打印出上个页面传递的参数。
		this.postid=option.id;
		this.getdsList(this.postid);
		
		if(option.type=='fenxiang'){
			if(server.userinfo.Auid==""||server.userinfo.Auid==null){
				uni.showLoading({
					title: '加载中'
				});
				uni.login({
				    provider: 'weixin',
				    success: (res) => {
						console.log(res);
						uni.request({
							method:'POST',
							url: 'https://api.angeli.top/reg.php?type=wxlogin', //仅为示例，并非真实接口地址。
							data: {
								code: res.code,
								tuijianId:this.tuijianren
							},
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: (res) => {
								console.log(res);
								if(res.data.code=="0"){
									server.usersk=res.data.data.session_key
									uni.showToast({
										title: "欢迎你，游客",
										position:'bottom',
										icon:'none'
									});
									this.AvatarUrl="https://sz.oss.data.angeli.top/angeli-image/1562320238188110.png";
								}else if(res.data.code=="1" ||res.data.code=="2"){
									this.AvatarUrl=res.data.data.AvatarUrl;
									this.username=res.data.data.UserName;
									this.zhongcao=res.data.data.ZhongcaoCount;
									this.guanzhu=res.data.data.FollowedCount;
									this.fensi=res.data.data.FollowerCount;
									this.dengji=res.data.data.Rank;
									this.userid=res.data.data.Auid;
									this.userInfo=res.data.data;
									server.userinfo=res.data.data;
									server.cookie=res.header['Set-Cookie'];
									console.log("记录cookie：",server.cookie)
									if(res.data.code=="2"){
										uni.showToast({
											title: res.data.msg,
											position:'bottom',
											icon:'none'
										})
									}else{
										if(this.userInfo.VIPEndTime>0){
											uni.showToast({
												title: '欢迎VIP：'+this.username,
												position:'bottom',
												icon:'none'
											})
										}else{
											uni.showToast({
												title: '欢迎你,'+this.username,
												position:'bottom',
												icon:'none'
											})
										}
									}
									
								}
							},
							complete: () => {
								this.getpostinfo(option.id);
								uni.hideLoading();
							},
							fail:(src) =>{
								uni.showToast({
									title: "未知原因，登录失败！",
									position:'bottom',
									icon:'none'
								})
							},
						});
				    },
				    fail: (err) => {
						uni.showToast({
							title: "登录失败",
							position:'bottom'
						}),
						this.getpostinfo(option.id);
				        console.error('授权登录失败：' + JSON.stringify(err));
				    }
				})
			}else{
				if(server.userinfo.VIPEndTime>0){
					uni.showToast({
						title: '欢迎VIP：'+server.userinfo.username,
						position:'bottom',
						icon:'none'
					})
				}else{
					uni.showToast({
						title: '欢迎你,'+server.userinfo.username,
						position:'bottom',
						icon:'none'
					})
					
				}
				this.getpostinfo(option.id);
			}	
		}else{
			this.getpostinfo(option.id);
		}
		
		console.log(this.dslist)
	},
	
	methods: {
		getpostinfo:function(postid){
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=outPostInfo', 
				data: {
					id: postid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log("————————————帖子详情——————————");
					this.postInfo=res.data.data;
					console.log(this.postInfo);
					this.getplLista(postid)
				},
				complete() {
					
				}
			});
		},
		getplLista:function(postid){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=getpl', 
				data: {
					postid: postid,
					xu:this.shunxu,
					count:40,
					page:1
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log("————————————评论详情——————————");
					this.pllist=res.data.data;
					console.log(this.pllist);
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		setshunxu(){
			if(this.shunxu){
				this.shunxu=false;
			}else{
				this.shunxu=true;
			}
			console.log(this.shunxu)
			this.getplLista(this.postid)
		},
		getpllist(CommentsId){
			uni.navigateTo({
				url: 'plinfo?CommentsId='+CommentsId+"&postId="+this.postid
			})
		},
		getClass:function(id){
			uni.navigateTo({
				url: '../classPost/classPost?id='+id
			})
		},
		caidan:function(resa){
			console.log(resa)
			this.Dindex=resa;
			if(resa.AuthorId==server.userinfo.Auid){
				this.menuList=['生成海报', '举报','删除帖子']
			}else{
				this.menuList=['生成海报', '举报']
			}
			uni.showActionSheet({
				itemList:this.menuList,
				success: (res) =>{
					switch(res.tapIndex){
						case 0:
							uni.showToast({
								title: "生成海报还在内测中",
								position:'bottom',
								icon:'none'
							});
							break;
						case 1:
							uni.showToast({
								title:'举报成功',
								position:'bottom',
								icon:'none'
							});
							this.$jubao(resa.PostsId,server.userinfo.Auid,resa.AuthorId,'没有理由');
							break;
						case 2:
						uni.showToast({
							title:'已删除',
							position:'bottom',
							icon:'none'
						});
							this.$delPost(resa.PostsId);
							break;
						default:
							
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		getbieren:function(e){
			if(e==server.userinfo.Auid){
				uni.navigateTo({
					url: '../i/i'
				})
			}else{
				uni.navigateTo({
					url: '../i/bieren?auid='+e
				})
			}
		},
		getdsList:function(postid){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=getDashangList&postId="+postid, //仅为示例，并非真实接口地址。
				data: {
				
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.dslist=res.data.data
						console.log("打赏输出")
						console.log(this.dslist);
						this.$forceUpdate()
					}else{
						this.dslist=0;
					}
				},
				complete() {
					
				}
			});
		},
		getList:function(){
			uni.navigateTo({
				url:"dashang?id="+this.postid
			})
		},
		getDashang:function(){
			if(this.postInfo.AuthorId==server.userinfo.Auid){
				uni.showToast({
					title: "不能给自己打赏",
					position:'bottom',
					icon:'none'
				});
				return;
			}
			console.log(this.monnumber);
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=dashang", //仅为示例，并非真实接口地址。
				data: {
					toid: this.postInfo.AuthorId,
					postid:this.postid,
					number:this.monnumber
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.showVip=false;
						this.gaodu='-710px';
						this.yanse='rgba(0,0,0,0)'
						uni.showToast({
							title: "打赏成功！",
							position:'bottom',
							icon:'none'
						});
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
			
		},
		hideVip:function(){
			this.showVip=false;
			this.gaodu='-710px';
			this.yanse='rgba(0,0,0,0)'
		},
		xuanze:function(e){
			switch (e){
				case 1:
					this.monnumber=1
					break;
				case 2:
					this.monnumber=5
					break;
				case 3:
					this.monnumber=10
					break;
				case 4:
					this.monnumber=20
					break;
				default:
					this.monnumber=1
					break;
			}
			this.xzId=e
			/* 新思路,点赞改变列表状态可以使用该方法,:class="当前ID是否等于列子ID，如果是那么就点赞，如果不是就没有点赞，"
			但是,这样只能存在一个点赞状态,其他的状态就会消失,带思考 */
		},
		showKaitong:function(){
			this.showVip=true
			this.gaodu='0px'
			this.yanse='rgba(0,0,0,0.4)'
		},
		Like:function(postid,auid,give,zc){
			if(server.userinfo.Auid==""||server.userinfo.Auid==null){
				uni.showToast({
					title: "你还没有登录，请登录后再来吧",
					position:'bottom',
					icon:'none'
				});
				setTimeout(function () {
					uni.navigateTo({
						url: '../reg/reg'
					})
				}, 1200);
				return;
			}
			if(auid==server.userinfo.Auid){
				uni.showToast({
					title: "不能给自己种草",
					position:'bottom',
					icon:'none'
				});
				return;
			}
			if(give===true){
				var modea='del'
			}else{
				var modea='add'
			}
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=Like", 
				data: {
					fuid: auid,
					postid:postid,
					mode:modea
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						if(modea=='add'){
							this.postInfo.Give=true;
							this.postInfo.ZhongcaoCount=Number(zc)+1;
							uni.showToast({
								title: "种草成功！",
								position:'bottom',
								icon:'none'
							});
						}else{
							this.postInfo.Give=false;
							this.postInfo.ZhongcaoCount=Number(zc)-1;
							uni.showToast({
								title: "取消种草成功！",
								position:'bottom',
								icon:'none'
							});
						}
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: "种草失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		pinglun:function(e){
			this.plnr=e.target.value;
			if(this.plnr.indexOf('@')==0){
				this.huifu=true;
			}else{
				this.huifu=false;
			}
		},
		onShareAppMessage(res) {
			if (res.from === 'button') {// 来自页面内分享按钮
			  console.log(res.target)
			}
			return {
			  title: this.postInfo.Content,
			  path:'/pages/postinfo/postinfo?id='+this.postid+'&type=fenxiang',
			  desc:this.postInfo.Content,
			  imageUrl:this.postInfo.PictureId[0]
			}
		},
		showImage(res,c){
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function (res) {
						
						uni.showToast({
							title: '选中了第' + (res.tapIndex + 1) + '个按钮',
							position:'bottom',
							icon:'none'
						});
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		sendpl:function(e){
			console.log(this.plnr);
			if(this.plnr=="null" ||this.plnr==""){
				uni.showToast({
					title: "请输入评论内容！",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(this.plnr.length>300 || this.plnr.length<1 ){
				uni.showToast({
					title: "内容最少1个字，最多300字",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(this.huifuid && this.huifu==true){
				//回复评论模式
				//console.log("回复评论",this.plnr)
				let qian=this.plnr.substring(0,this.plnr.indexOf(':')+1);
				let nr=this.plnr.replace(qian,"");
				uni.showLoading({
					title: '正在提交数据'
				});
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/post.php?type=hfpl', //仅为示例，并非真实接口地址。
					data: {
						id: this.huifuid,
						txt:nr,
						postid:this.postid,
						uid:this.pluid,
						auid:server.userinfo.Auid
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						if(res.data.code=='1'){
							this.setvar="";
							this.plnr=''
							uni.request({
								method:'GET',
								url: 'https://api.angeli.top/post.php?type=getpl', //仅为示例，并非真实接口地址。
								data: {
									postid: this.postid
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded',
									'Cookie':server.cookie
								},
								success: (res) => {
									console.log("————————————评论详情——————————");
									this.pllist=res.data.data;
									this.plnr==""
									console.log(this.pllist);
									
								}
							});
						}
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
						
					
					},
					complete() {
						setTimeout(function () {
							uni.hideLoading()
						}, 2000);
					}
				});
				
			}else{
				//评论模式
				uni.showLoading({
					title: '正在提交数据'
				});
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/post.php?type=pl', //仅为示例，并非真实接口地址。
					data: {
						auid: server.userinfo.Auid,
						txt:this.plnr,
						postid:this.postid,
						uid:this.postInfo.AuthorId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						if(res.data.code=='1'){
							this.plnr=''
							this.setvar="";
							uni.request({
								method:'GET',
								url: 'https://api.angeli.top/post.php?type=getpl', //仅为示例，并非真实接口地址。
								data: {
									postid: this.postid
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded',
									'Cookie':server.cookie
								},
								success: (res) => {
									console.log("————————————评论详情——————————");
									this.pllist=res.data.data;
									console.log(this.pllist);
									this.plnr==""
									
								}
							});
						}
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
						
					},
					complete() {
						setTimeout(function () {
							uni.hideLoading()
						}, 2000);
					}
				});
			}
		},
		dianzan:function(plid,index){
			console.log("点赞")
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=dianzan', //仅为示例，并非真实接口地址。
				data: {
					postid: plid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						var give=this.pllist[index].Give;
						console.log(give);
						give++;
						console.log(give);
						this.$set(this.pllist[index],`Give`,give);
					}
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none',
						duration:2000
					});
				}
			});
		},
		huifua:function(e,n,plid,uid){
			this.huifuid=e;//e=评论id
			this.setvar="@"+n+": ";
			this.huifu=true;
			this.pluid=uid;//父级评论id
			console.log("回复",e,n)
		}
	}
}
